/** Базовые анимации: */
@import "keyframes";

/** Стили для полноэкранного блока: */
// @import "fullscreen";

/*
* (i) Стили будут применяться ко
* всем классам содержащим *__container
* Например header__container, main__container и т.д.
*/
[class*="__container"] {
  max-width: rem($maxWidthContainer);
  margin: 0 auto;
  padding: 0 rem($mainOffset);
}

// Стили отзывчивых картинок (IBG) ============================================
[class*="-ibg"] {
  position: relative;

  img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
  }
}

[class*="-ibg_contain"] {
  img {
    object-fit: contain;
  }
}

// Оболочка ===================================================================
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;

  /** Поменять на clip - если используется в проекте position: sticky */
  overflow: hidden;
  position: relative;
}

.page {
  position: relative;
  flex: 1 1 auto;
  background-color: var(--primary-color);
  // margin-top: $headerHeight + px;
  .text {
    padding: rem(20) rem(10);
    margin: rem(5) 0;
    border-radius: rem(15);
  }
  .text_1 {
    background: greenyellow;
  }
  .text_2 {
    background: yellow;
  }
  .text_3 {
    background: #ffb8c1;
  }
  .text_4 {
    background: #ffdc8d;
  }
  .text_5 {
    background: #0c4a88;
  }
  .text_6 {
    background: #0b8c47;
  }
  .text_7 {
    background: #b2b82a;
  }
  .text_8 {
    background: #53df83;
  }
  .text_9 {
    background: #47d2e9;
  }
  .text_10 {
    background: #eeeeee;
  }
  .text_11 {
    background: #ffc153;
  }
  .text_12 {
    background: #47b39d;
  }

  [data-observ] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: rem(2);
    background: transparent;
    pointer-events: none;
  }

  &_start {
    padding-top: rem($headerHeight);
  }
}
